import type { RequestHandler } from "express";

import { underStudyLogin } from "./login.js";
import { UNDER_STUDY_SERVER } from "./utils.js";
import type { AuthLoginFailedResponse } from "../auth/index.js";
import {
  ExpiredResponse,
  MissingArgResponse,
  MissingCredentialResponse,
  UnknownResponse,
} from "../config/index.js";
import type {
  CommonFailedResponse,
  EmptyObject,
  LoginOptions,
} from "../typings.js";
import { EDGE_USER_AGENT_HEADERS } from "../utils/index.js";

export interface UnderGradeDetailOptions extends LoginOptions {
  /**  */
  gradeCode: string;
}

export interface UnderScoreDetail {
  /**  */
  name: string;
  /**  */
  score: number;
  /**  */
  percent: number;
}

interface RawUnderGradeDetailItem {
  /**  */
  zcj: string;

  /**  1  */
  bl1: number;
  /**  2  */
  bl2: number;
  /**  3  */
  bl3: number;
  /**  4  */
  bl4: number;
  /**  5  */
  bl5: number;

  /**  1  */
  bl1mc: string;
  /**  2  */
  bl2mc: string;
  /**  3  */
  bl3mc: string;
  /**  4  */
  bl4mc: string;
  /**  5  */
  bl5mc: string;

  /**  1 */
  cj1: number | "";
  /**  2 */
  cj2: number | "";
  /**  3 */
  cj3: number | "";
  /**  4 */
  cj4: number | "";
  /**  5 */
  cj5: number | "";

  /**  */
  kkbmmc: string;

  kkjysmc: "";
  isrk: "";
}

interface RawUnderGradeSuccessResult {
  code: 0;
  data: RawUnderGradeDetailItem[];
  message: string;
}

interface RawUnderGradeFailedResult {
  code: number;
  data: unknown;
  message: string;
}

type RawUnderGradeResult =
  | RawUnderGradeSuccessResult
  | RawUnderGradeFailedResult;

const getGradeDetail = ({
  cj1,
  cj2,
  cj3,
  cj4,
  cj5,
  bl1,
  bl2,
  bl3,
  bl4,
  bl5,
  bl1mc,
  bl2mc,
  bl3mc,
  bl4mc,
  bl5mc,
}: RawUnderGradeDetailItem): UnderScoreDetail[] => {
  const results: UnderScoreDetail[] = [];

  if (bl1 > 0) results.push({ name: bl1mc, score: Number(cj1), percent: bl1 });
  if (bl2 > 0) results.push({ name: bl2mc, score: Number(cj2), percent: bl2 });
  if (bl3 > 0) results.push({ name: bl3mc, score: Number(cj3), percent: bl3 });
  if (bl4 > 0) results.push({ name: bl4mc, score: Number(cj4), percent: bl4 });
  if (bl5 > 0) results.push({ name: bl5mc, score: Number(cj5), percent: bl5 });

  return results;
};

export interface UnderGradeDetailSuccessResponse {
  success: true;
  data: UnderScoreDetail[];
}

export type UnderGradeDetailResponse =
  | UnderGradeDetailSuccessResponse
  | AuthLoginFailedResponse
  | CommonFailedResponse;

const UNDER_GRADE_DETAIL_RESPONSE: UnderGradeDetailSuccessResponse = {
  success: true,
  data: [
    {
      name: "",
      score: 80,
      percent: 20,
    },
    {
      name: "",
      score: 90,
      percent: 80,
    },
  ],
};

export const getUnderGradeDetail = async (
  cookieHeader: string,
  gradeCode: string,
): Promise<UnderGradeDetailResponse> => {
  const queryUrl = `${UNDER_STUDY_SERVER}/new/student/xskccj/getDetail?cjdm=${gradeCode}`;

  const response = await fetch(queryUrl, {
    headers: {
      Accept: "application/json, text/javascript, */*; q=0.01",
      Cookie: cookieHeader,
      Referer: `${UNDER_STUDY_SERVER}/new/student/xskccj/kccjList.page`,
      ...EDGE_USER_AGENT_HEADERS,
    },
  });

  if (response.headers.get("Content-Type")?.includes("text/html"))
    return ExpiredResponse;

  const data = (await response.json()) as RawUnderGradeResult;

  if (data.code !== 0) {
    if (data.message === "") return ExpiredResponse;

    throw new Error(data.message);
  }

  const gradeDetail = getGradeDetail(
    (data.data as RawUnderGradeDetailItem[])[0],
  );

  return {
    success: true,
    data: gradeDetail,
  };
};

export const underStudyGradeDetailHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  UnderGradeDetailOptions
> = async (req, res) => {
  try {
    const { id, password, authToken, gradeCode } = req.body;

    if (!gradeCode) return res.json(MissingArgResponse(""));

    if (id && password && authToken) {
      const result = await underStudyLogin({ id, password, authToken });

      if (!result.success) return res.json(result);

      req.headers.cookie = result.cookieStore.getHeader(UNDER_STUDY_SERVER);
    } else if (!req.headers.cookie) {
      return res.json(MissingCredentialResponse);
    }

    const cookieHeader = req.headers.cookie;

    if (cookieHeader.includes("TEST"))
      return res.json(UNDER_GRADE_DETAIL_RESPONSE);

    return res.json(await getUnderGradeDetail(cookieHeader, gradeCode));
  } catch (err) {
    const { message } = err as Error;

    console.error(err);

    return res.json(UnknownResponse(message));
  }
};
