import type { RequestHandler } from "express";

import { actionLogin } from "./login.js";
import { ACTION_MAIN_PAGE, ACTION_SERVER } from "./utils.js";
import type { AuthLoginFailedResponse } from "../auth/login.js";
import {
  ActionFailType,
  MissingCredentialResponse,
  UnknownResponse,
} from "../config/index.js";
import type {
  CommonFailedResponse,
  CommonSuccessResponse,
  EmptyObject,
  LoginOptions,
} from "../typings.js";
import type { VPNLoginFailedResponse } from "../vpn/login.js";

const EMAIL_INFO_URL = `${ACTION_SERVER}/extract/getEmailInfo`;

interface RawEmailData {
  /**  */
  from: string;
  /** ID */
  id: string;
  /**  */
  receivedDate: number;
  /**  */
  sentDate: number;
  /**  */
  size: number;

  /**  */
  subject: string;
  to: string;

  fid: 1;
  flags: {
    read: boolean;
    popRead: boolean;
    attached: boolean;
  };
}

interface RawRecentMailSuccessResponse {
  success: true;
  count: string;
  emailList: {
    suc: true;
    ver: 0;
    /**  */
    account_name: string;
    con: {
      /**  */
      total: number;
      var: RawEmailData[];
    };
  };
}

interface RawRecentMailFailedResponse {
  emailList: {
    suc: false;
    ver: 0;
    error_code: string;
  };
}

type RawRecentMailResponse =
  | RawRecentMailSuccessResponse
  | RawRecentMailFailedResponse;

export interface EmailData {
  /**  */
  subject: string;
  /**  */
  receivedDate: number;
  /**  */
  name: string;
  /**  */
  email: string;
  /**  ID */
  mid: string;
  /**  */
  unread: boolean;
}

const getRecentEmailData = ({
  subject,
  receivedDate,
  from,
  id,
  flags,
}: RawEmailData): EmailData => ({
  subject,
  receivedDate,
  name: /"(.*)"/.exec(from)?.[1] ?? from,
  email: /<(.*)>/.exec(from)?.[1] ?? from,
  mid: id,
  unread: !flags.read,
});

export interface RecentMailData {
  /**  */
  unread: number;
  /**  */
  emails: EmailData[];
}

export type ActionRecentMailSuccessResponse =
  CommonSuccessResponse<RecentMailData>;

export type ActionRecentMailFailedResponse = CommonFailedResponse<
  ActionFailType.NotInitialized | ActionFailType.Unknown
>;

export type ActionRecentMailResponse =
  | ActionRecentMailSuccessResponse
  | AuthLoginFailedResponse
  | VPNLoginFailedResponse
  | ActionRecentMailFailedResponse;

const TEST_RECENT_EMAIL_RESPONSE: ActionRecentMailSuccessResponse = {
  success: true,
  data: {
    unread: 1,
    emails: Array<EmailData>(10).fill({
      subject: "",
      receivedDate: Date.now(),
      name: "",
      email: "admin@example.com",
      mid: "1",
      unread: true,
    }),
  },
};

export const getRecentEmails = async (
  cookieHeader: string,
): Promise<ActionRecentMailResponse> => {
  const checkResponse = await fetch(EMAIL_INFO_URL, {
    method: "POST",
    headers: {
      Accept: "application/json, text/javascript, */*; q=0.01",
      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      Cookie: cookieHeader,
      Referer: ACTION_MAIN_PAGE,
    },
    body: `domain=nenu.edu.cn&type=1&format=json`,
  });

  const checkResult = (await checkResponse.json()) as RawRecentMailResponse;

  if (
    "success" in checkResult &&
    checkResult.success &&
    checkResult.emailList.con
  )
    return {
      success: true,
      data: {
        unread: Number(checkResult.count),
        emails: checkResult.emailList.con.var.map(getRecentEmailData),
      },
    };

  return {
    success: false,
    type: ActionFailType.NotInitialized,
    msg: "",
  };
};

export const actionRecentEmailHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  LoginOptions,
  LoginOptions & { mid?: string }
> = async (req, res) => {
  try {
    const { id, password, authToken } = req.body;

    if (id && password && authToken) {
      const result = await actionLogin({ id, password, authToken });

      if (!result.success) return res.json(result);

      req.headers.cookie = result.cookieStore.getHeader(ACTION_SERVER);
    } else if (!req.headers.cookie) {
      return res.json(MissingCredentialResponse);
    }

    const cookieHeader = req.headers.cookie;

    if (cookieHeader.includes("TEST"))
      return res.json(TEST_RECENT_EMAIL_RESPONSE);

    return res.json(await getRecentEmails(cookieHeader));
  } catch (err) {
    const { message } = err as Error;

    console.error(err);

    return res.json(UnknownResponse(message));
  }
};
