import type { RequestHandler } from "express";

import { actionLogin } from "./login.js";
import { ACTION_SERVER } from "./utils.js";
import type { AuthLoginFailedResponse } from "../auth/index.js";
import type { ActionFailType } from "../config/index.js";
import {
  ExpiredResponse,
  MissingCredentialResponse,
  UnknownResponse,
} from "../config/index.js";
import type {
  CommonFailedResponse,
  CommonListSuccessResponse,
  EmptyObject,
  LoginOptions,
} from "../typings.js";
import type { VPNLoginFailedResponse } from "../vpn/login.js";

const NOTICE_LIST_QUERY_URL = `${ACTION_SERVER}/page/queryList`;

export interface NoticeListOptions extends LoginOptions {
  /**
   * 
   *
   * @default "notice"
   */
  type?: "notice" | "news";
  /**
   * 
   *
   * @default 20
   */
  size?: number;
  /**
   * 
   *
   * @default 1
   */
  current?: number;
}

interface RawNoticeItem {
  LLCS: number;
  FBSJ: string;
  KEYWORDS_: string;
  ID__: string;
  SFZD: string;
  FLAG: string;
  RN: number;
  CJBM: string;
  TYPE: "notice";
}

interface RawNoticeListData {
  data: RawNoticeItem[];
  pageIndex: number;
  totalPage: number;
  pageSize: number;
  totalCount: number;
}

export interface NoticeInfo {
  title: string;
  from: string;
  time: string;
  id: string;
}

const getNoticeItem = ({
  ID__: id,
  CJBM: from,
  KEYWORDS_: title,
  FBSJ: time,
}: RawNoticeItem): NoticeInfo => ({
  id,
  title,
  from,
  time,
});

export interface NoticeListSuccessResponse
  extends CommonListSuccessResponse<NoticeInfo[]> {
  size: number;
  count: number;
}

export type NoticeListResponse =
  | NoticeListSuccessResponse
  | AuthLoginFailedResponse
  | VPNLoginFailedResponse
  | CommonFailedResponse<ActionFailType.Expired | ActionFailType.Unknown>;

const TEST_NOTICE_LIST: NoticeListSuccessResponse = {
  success: true,
  data: Array<NoticeInfo>(10).fill({
    title: "",
    from: "",
    time: "2021-01-01",
    id: "test",
  }),
  count: 10,
  size: 20,
  current: 1,
  total: 1,
};

export const getNoticeList = async (
  cookieHeader: string,
  type: string,
  size: number,
  current: number,
): Promise<NoticeListResponse> => {
  const response = await fetch(NOTICE_LIST_QUERY_URL, {
    method: "POST",
    headers: {
      Accept: "application/json, text/javascript, */*; q=0.01",
      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      Cookie: cookieHeader,
      Referer: `${ACTION_SERVER}/basicInfo/studentPageTurn?type=lifeschool`,
    },
    body: new URLSearchParams({
      type,
      _search: "false",
      nd: Date.now().toString(),
      limit: size.toString(),
      page: current.toString(),
    }),
    redirect: "manual",
  });

  if (response.status === 302) return ExpiredResponse;

  const { data, pageIndex, pageSize, totalCount, totalPage } =
    (await response.json()) as RawNoticeListData;

  if (!data.length)
    throw new Error(`: ${JSON.stringify(data, null, 2)}`);

  return {
    success: true,
    data: data.map(getNoticeItem),
    count: totalCount,
    size: pageSize,
    current: pageIndex,
    total: totalPage,
  } as NoticeListSuccessResponse;
};

export const noticeListHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  NoticeListOptions
> = async (req, res) => {
  try {
    const {
      id,
      password,
      authToken,
      type = "notice",
      size = 20,
      current = 1,
    } = req.body;

    if (id && password && authToken) {
      const result = await actionLogin({ id, password, authToken });

      if (!result.success) return res.json(result);

      req.headers.cookie = result.cookieStore.getHeader(NOTICE_LIST_QUERY_URL);
    } else if (!req.headers.cookie) {
      return res.json(MissingCredentialResponse);
    }

    const cookieHeader = req.headers.cookie;

    if (cookieHeader.includes("TEST")) return res.json(TEST_NOTICE_LIST);

    return res.json(await getNoticeList(cookieHeader, type, size, current));
  } catch (err) {
    const { message } = err as Error;

    console.error(err);

    return res.json(UnknownResponse(message));
  }
};
